.main-nav {
  position: relative;
}

.main-nav__toggle {
  position: absolute;
  top: -48px;
  right: 31px;
  z-index: 2;

  display: block;
  width: 20px;
  height: 14px;
  background-color: transparent;

  cursor: pointer;
  border: none;
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 2px;

  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
}

.main-nav--opened .main-nav__toggle {
  &::before,
  &::after {
    content: "";

    position: absolute;
    top: 6px;
    left: -2px;

    width: 25px;
    height: 2px;

    background-color: #000000;
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:active::before,
  &:active::after {
    background-color: rgba(0, 0, 0, 0.3);
  }
}

.main-nav--closed .site-list {
  display: none;
}

.main-nav__wrapper {
  display: flex;
  flex-direction: column;
}

.main-nav__wrapper--hidden {
  display: none;
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav--nojs .main-nav__wrapper {
  position: static;
  min-height: 0;
}

.main-nav--nojs .site-list {
  display: block;
}

.main-nav--nojs .site-list__item--active a {
  padding-right: 21px;
  padding-left: 21px;

  text-align: left;
}

@media (min-width: @tablet-width-viewport) {
  .main-nav--closed .site-list {
    display: flex;
  }

  .main-nav {
    .container(@tablet-width);
  }

  .main-nav__toggle {
    display: none;
  }

  .main-nav__wrapper--hidden {
    display: block;
  }

  .main-nav__wrapper {
    flex-direction: row;
  }
}


@media (min-width: @desktop-width-viewport) {
  .main-nav {
    .container(@desktop-width);
  }
}
